import { DeepPartial } from '../core'
import { CampaignPoint } from './campaign-points.model'

export function CampaignPointMock(
  options?: DeepPartial<CampaignPoint>
): CampaignPoint {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'CAMPAIGN_POINT_ID',
    walletId: options?.walletId || 'WALLET_ID',
    walletAddress: options?.walletAddress || 'WALLET_ADDRESS',
    campaignId: options?.campaignId || 'CAMPAIGN_ID',
    points: options?.points || 0,
    perDay: options?.perDay || 0,

    createdAt: options?.createdAt || now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: options?.updatedAt || now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}
